.aspectmod h1 {
  display: block;
}

.form-group.aspectmod .dialogHeader {
  flex-basis: auto;
  flex-direction: row;
  display: flex;
}

.form-group.aspectmod .dialogEntry {
  width: 10em;
  min-width: 10em;
}

.form-group.aspectmod .inspirationInput {
  width: 5em;
}

div .aspectmod .e1die img {
  background-color: #b9fb9d;
  background-blend-mode: multiply;
}

div .aspectmod .basedie > img {
  background-color: #f5adff;
  background-blend-mode: multiply;
}

div .aspectmod .creaturedie > img {
  background-color: #fca1a1;
  background-blend-mode: multiply;
}

div .aspectmod .rewarddie > img {
  background-color: #ffd500;
  background-blend-mode: multiply;
}

div .aspectmod .aspectdie > img {
  width: 32px;
  margin-top: 5px;
  margin-right: 5px;
  float: left;
  vertical-align: top;
}